<template>
    <el-container>
        <el-header>
            系统设置
        </el-header>
        <el-main>
            <el-card shadow="hover">
                <el-tabs v-model="tabsActive">
                    <el-tab-pane label="数据拟合" name="1">
                        <div style="margin: 2em; color: #868686; font-size: 15px">
                            “阻止自动拟合”为会话级别的功能，这意味着在您关闭这个网页时它就会失效。<br>
                            开启“阻止自动拟合”后，上传、删除水位或水域面积记录时，系统将不会根据数据变化拟合模型。<br>
                            <br>
                            您也可以点击下方的按钮手动让系统使用现有记录进行拟合操作。
                        </div>
                        <el-divider></el-divider>
                        <el-form label-width="20%">
                            <el-form-item label="阻止自动拟合">
                                <el-switch active-color="#F71B3A" @change="handleStopFittingChange"
                                           v-model="fit.stopFitting"></el-switch>
                            </el-form-item>
                            <el-form-item label="立即拟合">
                                <el-button @click="handleFitLevelNowCLick">仅拟合日期-水位</el-button>
                                <el-button @click="handleFitAreaNowCLick">仅拟合水位-面积</el-button>
                                <el-button @click="handleFitAllNowCLick">拟合所有模型</el-button>
                            </el-form-item>
                        </el-form>
                    </el-tab-pane>
                </el-tabs>
            </el-card>

        </el-main>
    </el-container>
</template>

<script>
    export default {
        name: "setting",
        props: [],
        data: function () {
            return {
                tabsActive: '1',
                fit: {
                    stopFitting: false,
                },
                gettingMeasured: false,
            }
        },
        computed: {

        },
        methods: {
            handleStopFittingChange: function () {
                if (this.fit.stopFitting) {
                    this.axios.get('/api/setting/pauseFitting', {
                        params: {
                            method: 'true',
                        }
                    }).then((response) => {
                        if (response.status === 200) {
                            this.$notify({
                                title: '关闭',
                                message: '系统自动拟合已关闭。',
                                type: 'success'
                            });
                        } else {
                            this.$notify({
                                title: '失败',
                                message: '连接错误！',
                                type: 'error'
                            });
                        }
                    })
                } else {
                    this.axios.get('/api/setting/pauseFitting', {
                        params: {
                            method: '',
                        }
                    }).then((response) => {
                        if (response.status === 200) {
                            this.$notify({
                                title: '开启',
                                message: '系统自动拟合已开启。',
                                type: 'success'
                            });
                        } else {
                            this.$notify({
                                title: '失败',
                                message: '连接错误！',
                                type: 'error'
                            });
                        }
                    })
                }
            },
            handleFitLevelNowCLick: function () {
                if (this.$store.getters.getReservoir === '') {
                    this.$notify({
                        title: '警告',
                        message: '未选择水库',
                        type: 'warning'
                    });
                    return;
                }
                this.axios.get('/api/setting/fitLevelNow').then((response) => {
                    if (response.status === 200) {
                        this.$notify({
                            title: '成功',
                            message: '日期-水位拟合完毕。',
                            type: 'success'
                        });
                    } else {
                        this.$notify({
                            title: '失败',
                            message: '连接错误！',
                            type: 'error'
                        });
                    }
                })
            },
            handleFitAreaNowCLick: function () {
                if (this.$store.getters.getReservoir === '') {
                    this.$notify({
                        title: '警告',
                        message: '未选择水库',
                        type: 'warning'
                    });
                    return;
                }
                this.axios.get('/api/setting/fitAreaNow').then((response) => {
                    if (response.status === 200) {
                        this.$notify({
                            title: '成功',
                            message: '水位-面积拟合完毕。',
                            type: 'success'
                        });
                    } else {
                        this.$notify({
                            title: '失败',
                            message: '连接错误！',
                            type: 'error'
                        });
                    }
                })
            },
            handleFitAllNowCLick: function () {
                if (this.$store.getters.getReservoir === '') {
                    this.$notify({
                        title: '警告',
                        message: '未选择水库',
                        type: 'warning'
                    });
                    return;
                }
                this.axios.get('/api/setting/fitAllNow').then((response) => {
                    if (response.status === 200) {
                        this.$notify({
                            title: '成功',
                            message: '所有模型拟合完毕。',
                            type: 'success'
                        });
                    } else {
                        this.$notify({
                            title: '失败',
                            message: '连接错误！',
                            type: 'error'
                        });
                    }
                })
            },
        },
        mounted() {
            this.axios.get('/api/setting/getPauseFittingStatus').then((response) => {
                if (response.status === 200) {
                    this.fit.stopFitting = response.data;
                } else {
                    this.$notify({
                        title: '失败',
                        message: '连接错误！',
                        type: 'error'
                    });
                }
            })
        }
    }
</script>

<style scoped>
    .el-card {
        border: 1px solid #d4d4d4;
        width: 100%;
    }
</style>
